.App {
  text-align: center;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
  opacity: 50%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  width: 100%;
  height: 100vh;
  background: #0C4A6E;
}
#background * {
  fill-opacity: 0;
  transition: fill-opacity 1s;
}

#background.finished * {
  fill-opacity: 1;
}
